@import "src/theme";

@import "~bootstrap/scss/_functions.scss";
@import "~bootstrap/scss/_variables.scss";
@import "~bootstrap/scss/mixins/_breakpoints.scss";

.item {

    // spacing in between each item
    padding-bottom: 40px;

    // add padding between each item on small screens
    @include media-breakpoint-down(sm) {
        margin: 0;
        padding-bottom: 15px;
    }

    // styling that only applies to left hand side
    .left {
        // align text away from border
        text-align: right;
        padding-right: 25px;

        // center text for small screens
        @include media-breakpoint-down(sm) {
            text-align: center;
            padding: 0;
        }

    }

    // styling that only applies to right hand side
    .right {
        // align text away from border
        text-align: left;
        padding-right: 25px;

        // add border betwen left and right
        border-left-style: solid;
        border-left-width: 2px;

        // center text and add some padding after content for small screens
        @include media-breakpoint-down(sm) {
            text-align: center;
            padding-right: 0;
            border-left-style: none;
            border-bottom-style: solid;
            border-bottom-width: 1px;
        }

        .hiddenToggle {
            font-family: $body-font-family;
            color: $accent-color;
            margin-bottom: 0.5rem;
        }
    }

    // styling that applies to both left and right hand side
    .col {
        padding-top: 20px;
        padding-bottom: 20px;

        // less padding between cols in small screen
        @include media-breakpoint-down(sm) {
            padding-top: 10px;
            padding-bottom: 10px;
        }

        .title {
            font-family: $header-font-family;
            font-size: 1.4em;
        }
        .subtitle {
            font-family: $body-font-family;
            font-size: 1.2em;
        }
        .body {
            font-family: $body-font-family;
            font-size: 1.2em;
        }
    }
}
